@import './base';

.postbox {
  display: flex;
  flex-direction: column;
  width: 500px;
  margin: 0 auto;
  background-color: #fff;
  border: 1px solid #dddfe2;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;

  &-wrapper {
    padding: 12px;
    border-bottom: 1px solid #e1e2e3;

    &:last-child {
      border-bottom: none;
      background-color: #f2f3f5;
    }
  }

  &-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    &-data {
      display: flex;
      flex-direction: row;
      height: 40px;
      line-height: 100%;
      margin-bottom: 10px;
    }

    &-title {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-left: 10px;

      &-name {
        align-self: flex-start;
        font-size: 0.875rem;
        color: #205a95;
      }

      &-created-at {
        align-self: flex-start;
        font-size: 0.75rem;
        color: #5f676f;

        .fas,
        .far {
          color: #8f949b;
          font-size: 0.6rem;
        }
      }
    }

    &-control {
      a {
        color: #5f676f;
      }
    }
  }

  &-message {
    &-text {
      padding: 10px 0;
      p {
        font-size: 0.875rem;
        line-height: 1.1875rem;
      }
    }
  }

  &-content {
    // border: 1px solid #d9d9d9;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);

    &-text {
      padding: 10px;

      h2 {
        font-family: Georgia, serif;
        font-weight: normal;
        font-size: 1.125rem;
        margin-bottom: 5px;
      }
      p {
        font-size: 0.75rem;
      }
      a {
        color: #1d2129;
      }
    }

    &-image {
      width: 100%;
      height: 249px;
      -webkit-box-shadow: inset 0 0 1px #ccc;
      -moz-box-shadow: inset 0 0 1px #ccc;
      box-shadow: inset 0 0 1px #ccc;
    }

    &-footer {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-end;
      margin-top: 5px;

      a {
        font-size: 0.8125rem;
        text-transform: uppercase;
        color: #5e676f;
      }
    }
  }

  &-social {
    &-control {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      margin-top: 10px;

      &-item {
        margin-left: 50px;
        font-size: 0.8125rem;
        font-weight: bold;

        &:first-child {
          margin-left: 0;
        }

        a {
          color: #5f676f;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;

          .owner-avatar {
            -webkit-border-radius: 50px;
            -moz-border-radius: 50px;
            border-radius: 50px;
          }
        }
      }
    }

    &-reactions {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #e1e2e3;
      padding-bottom: 10px;
      font-size: 0.75rem;

      &-icons {
        .reaction {
          position: relative;
          left: 0;
          top: 0;
          -webkit-border-radius: 50px;
          -moz-border-radius: 50px;
          border-radius: 50px;
          color: #fff;
          display: inline-block;
          width: 20px;
          height: 20px;
          padding-top: 4px;
          border: 2px solid #f2f3f5;

          &.fas,
          &.far {
            font-size: 0.5rem;
          }

          &.like {
            z-index: 3;
            background-color: #0085f8;
          }
          &.love {
            z-index: 2;
            margin-left: -8px;
            background-color: #ff5469;
          }
          &.sad {
            z-index: 1;
            margin: -8px;
            background-color: #ffd77e;
          }
        }
      }
      &-users {
        color: #205a95;
      }
      &-order {
      }
    }
  }

  &-share-counter {
    font-size: 0.75rem;
    padding: 10px 0;
  }

  &-comments {
    &-input {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      &-field {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: stretch;
        border: 1px solid #ccc;
        margin-left: 10px;
        padding: 10px;
        -webkit-border-radius: 50px;
        -moz-border-radius: 50px;
        border-radius: 50px;
        background-color: #fff;

        input {
          background: none;
          border: none;
          width: 300px;
          color: #5f676f;
          outline: none;
        }
      }
    }

    &-helper-text {
      padding-left: 55px;
      font-size: 0.6875rem;
      margin-bottom: 10px;
      color: #8d9399;
    }

    &-add-icon {
      margin-left: 7px;

      a {
        color: #8d9399;
        &:hover {
          color: #5f676f;
        }
      }
    }
  }

  &-comment {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;

    &-text {
      font-size: 0.8125rem;
      margin-left: 10px;

      &-bubble {
        background: #fff;
        padding: 15px;
        margin-bottom: 5px;
        -webkit-border-radius: 20px;
        -moz-border-radius: 20px;
        border-radius: 20px;

        strong {
          color: #205a95;
        }
      }

      &-footer {
        padding-left: 15px;
        margin-bottom: 20px;
      }
    }

    &-replies {
      .fas,
      .far {
        margin-right: 10px;
      }
    }
  }
}

.avatar {
  img {
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    border-radius: 50px;
  }
  &-sm {
    height: 30px;
    img {
      border: 1px solid #e5e5e5;
    }
  }
}

.btn-info {
  font-size: 0.75rem;
  font-weight: bold;
  padding: 5px 8px;
  color: #4a4f55;
  background-color: #f6f7f9;
  border: 1px solid #ced0d4;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  cursor: pointer;

  &:hover {
    background-color: #e9ebee;
  }
}
